import { Cell, Tag } from "@taroify/core"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function CloseTag() {
  const [visible, setVisible] = useState(true)
  return (
    <>
      {visible && (
        <Tag color="primary" size="medium" closeable onClose={() => setVisible(false)}>
          
        </Tag>
      )}
    </>
  )
}

export default function TagDemo() {
  return (
    <Page title="Tag " className="tag-demo">
      <Block variant="card" title="">
        <Cell title=" default">
          <Tag></Tag>
        </Cell>
        <Cell title=" primary">
          <Tag color="primary"></Tag>
        </Cell>
        <Cell title=" info">
          <Tag color="info"></Tag>
        </Cell>
        <Cell title=" success">
          <Tag color="success"></Tag>
        </Cell>
        <Cell title=" warning">
          <Tag color="warning"></Tag>
        </Cell>
        <Cell title=" danger">
          <Tag color="danger"></Tag>
        </Cell>
      </Block>
      <Block variant="card" title="">
        <Cell title="">
          <Tag color="primary" variant="outlined" children="" />
        </Cell>
        <Cell title="">
          <Tag color="primary" shape="round" children="" />
        </Cell>
        <Cell title="">
          <Tag color="primary" shape="roundRight" children="" />
        </Cell>
        <Cell title="">
          <Tag color="primary" shape="roundLeft" children="" />
        </Cell>
        <Cell title="">
          <CloseTag />
        </Cell>
      </Block>
      <Block variant="card" title="">
        <Cell title="">
          <Tag color="primary" children="" />
        </Cell>
        <Cell title="">
          <Tag color="primary" size="medium" children="" />
        </Cell>
        <Cell title="">
          <Tag color="primary" size="large" children="" />
        </Cell>
      </Block>
      <Block variant="card" title="">
        <Cell title="">
          <Tag style={{ backgroundColor: "#7232dd" }} children="" />
        </Cell>
        <Cell title="">
          <Tag style={{ backgroundColor: "#ffe1e1", color: "#ad0000" }} children="" />
        </Cell>
        <Cell title="">
          <Tag style={{ color: "#7232dd" }} variant="outlined" children="" />
        </Cell>
      </Block>
    </Page>
  )
}
